import React, { Component } from "react";
import MapView from "@arcgis/core/views/MapView";
import * as CIMSymbolRasterizer from "@arcgis/core/symbols/cim/CIMSymbolRasterizer";
import { CIMToRenderTree, getTemplate } from "../lib/utils/utils";
import {
  defaultCIMJSON,
  markerWithText,
  textCIMJSON,
  customCIMJSON2,
  EsriPin, pictureMarker
} from "../data/CIMJSONs";
import FeatureLayer from "@arcgis/core/layers/FeatureLayer";

type PrimitivesResrouce = {
  type: "primitives";
  url: string;
};

export type CustomResource = {
  type: "custom";
  items: Object[];
};

type SymbolResouceItem = {
  name: string;
  key: string;
  resource: PrimitivesResrouce | CustomResource;
};
const presetSymbolResources: SymbolResouceItem[] = [
  {
    name: "Icons",
    key: "icons",
    resource: {
      type: "primitives",
      url:
        "https://jsapi.maps.arcgis.com/sharing/rest/content/items/5d9115dc254649b6bf6f0c975984ad59",
    },
  },
  {
    name: "Shapes",
    key: "shapes",
    resource: {
      type: "primitives",
      url:
        "https://jsapi.maps.arcgis.com/sharing/rest/content/items/5465f2caf4b04c51a636ed3bea20172e",
    },
  },
  {
    name: "Custom",
    key: "custom",
    resource: {
      type: "custom",
      items: [EsriPin, markerWithText, textCIMJSON, pictureMarker],
    },
  },
];

export const SymbolItemContext = React.createContext<{
  item: any;
  symbolJSON: any;
  renderTree: any;
  currentLayer: any;
  currentLayerPath: any;
  tempLayers: any[];
  template: any;
  inGallery: boolean;
  symbolResources: SymbolResouceItem[];
  itemBeingDragged: any;
  cimSymbolRasterizer: any;
  layer: FeatureLayer | null;
  changeLayer: Function;
  changeSymbolJSON: Function;
  changeTempLayers: Function;
  updateSymbolJSONBasedOnNewValueOfCurrentLayer: Function;
  changeCurrentLayerPath: Function;
  changeInGallery: Function;
  updateSymbolResources: Function;
  updateItemBeingDragged: Function;
}>({
  item: {}, //webstyleItem
  symbolJSON: {
    //curent symbolJSON for editting.
    type: "CIMPointSymbol",
    symbolLayers: [],
  },
  renderTree: null,
  currentLayer: {}, //current Layer reference being editted.
  tempLayers: [], //new temp layers from gallery
  template: null, //for editing properties
  currentLayerPath: [],
  inGallery: false,
  symbolResources: [],
  itemBeingDragged: null,
  cimSymbolRasterizer: null,
  layer: null,
  changeLayer: () => {},
  changeSymbolJSON: () => {},
  changeTempLayers: () => {},
  updateSymbolJSONBasedOnNewValueOfCurrentLayer: () => {},
  changeCurrentLayerPath: () => {},
  changeInGallery: () => {},
  updateSymbolResources: () => {},
  updateItemBeingDragged: () => {},
});

class SymbolItemContextProvider extends Component {
  state = {
    item: {},
    symbolJSON: {
      type: "CIMPointSymbol",
      symbolLayers: [],
    },
    renderTree: null,
    currentLayer: {},
    currentLayerPath: [],
    tempLayers: [],
    template: null,
    inGallery: false,
    symbolResources: presetSymbolResources,
    itemBeingDragged: null,
    cimSymbolRasterizer: null,
    layer: null,
  };

  changeLayer = (layer: FeatureLayer) => {
    this.setState({
      layer: layer,
    });
  };

  changeSymbolJSON = (symbolJSON: any) => {
    this.setState({
      symbolJSON: symbolJSON,
      renderTree: CIMToRenderTree(symbolJSON),
    });
  };

  changeTempLayers = (layers: []) => {
    this.setState({
      tempLayers: layers,
    });
  };

  changeCurrentLayerPath = (array: []) => {
    let currentLayer = this.state.symbolJSON;
    for (let i = 0; i < array.length; i++) {
      currentLayer = currentLayer[array[i]];
    }
    this.setState({
      currentLayerPath: array,
      currentLayer: currentLayer,
      template: getTemplate(currentLayer),
    });
  };

  updateSymbolJSONBasedOnNewValueOfCurrentLayer = (
    propertyName: string,
    value: any
  ) => {
    const newSymbolJSON = { ...this.state.symbolJSON };
    let currentLayer: any = newSymbolJSON;
    for (let i = 0; i < this.state.currentLayerPath.length; i++) {
      currentLayer = currentLayer[this.state.currentLayerPath[i]];
    }
    currentLayer[propertyName] = value;
    this.changeSymbolJSON(newSymbolJSON);
  };

  changeInGallery = (g: any) => {
    this.setState({
      inGallery: g,
    });
  };

  updateSymbolResources = (symbolResources: SymbolResouceItem[]) => {
    this.setState({
      symbolResources,
    });
  };

  updateItemBeingDragged = (item: any) => {
    this.setState({
      itemBeingDragged: item,
    });
  };

  componentDidMount() {
    this.setState({
      cimSymbolRasterizer: new CIMSymbolRasterizer.CIMSymbolRasterizer({ wkid: 102100 }, true),
    });
  }

  render() {
    return (
      <SymbolItemContext.Provider
        value={{
          ...this.state,
          changeSymbolJSON: this.changeSymbolJSON,
          changeTempLayers: this.changeTempLayers,
          changeLayer: this.changeLayer,
          updateSymbolJSONBasedOnNewValueOfCurrentLayer: this
            .updateSymbolJSONBasedOnNewValueOfCurrentLayer,
          changeCurrentLayerPath: this.changeCurrentLayerPath,
          changeInGallery: this.changeInGallery,
          updateSymbolResources: this.updateSymbolResources,
          updateItemBeingDragged: this.updateItemBeingDragged,
        }}
      >
        {this.props.children}
      </SymbolItemContext.Provider>
    );
  }
}

export default SymbolItemContextProvider;
