import React, { useState, useEffect } from "react";
import "react-tabs/style/react-tabs.css";
import { hexToRgb, CIMColorToHex } from "../lib/utils/utils";
import useDebounce from "../hooks/useDebounce";
import { TextSymbolTemplate } from "../data/templates";

export const SymbolPropertyItem = (props: any) => {
  const { propertyName, inputType, name, value, unit, onChange, keys } = props; //textSymbol has keys instead of key for color

  const handleInputChange = (event: React.ChangeEvent) => {
    const target = event.target as HTMLInputElement;
    if (target.type === "number") {
      onChange(propertyName, Number((event.target as HTMLInputElement).value));
    } else {
      onChange(propertyName, (event.target as HTMLInputElement).value);
    }
  };

  const handleCheckboxChange = (event: React.ChangeEvent) => {
    onChange(propertyName, (event.target as HTMLInputElement).checked);
  };

  const handleCustomInputChange = (newValue: any) => {
    if (propertyName) {
      onChange(propertyName, newValue);
    } else if (keys) {
      //text symbol color
      const clonedValue = { ...value };
      let pointer = clonedValue;
      for (let i = 1; i < keys.length - 1; i++) {
        pointer = pointer[keys[i]];
      }
      pointer[keys[keys.length - 1]] = newValue;
      onChange(keys[0], clonedValue);
    }
  };

  const getValue = (value: any, keys: any) => {
    let pointer = value;
    for (let i = 1; i < keys.length; i++) {
      pointer = pointer[keys[i]];
    }

    return pointer;
  };

  switch (inputType) {
    case "number":
      return (
        <div className="symbolPropertyItem">
          <span className="numberInputLabel">{name}</span>
          <input onChange={handleInputChange} value={value} type="number" />
          <span>{unit}</span>
        </div>
      );
    case "text":
      return (
        <div className="symbolPropertyItem">
          <span className="inputLabel">{name}</span>
          <input onChange={handleInputChange} type="text" value={value} />
        </div>
      );
    case "imgURI":
      return (
        <div className="symbolPropertyItem">
          <span className="inputLabel">{name}</span>
          <textarea onChange={handleInputChange} value={value}></textarea>
        </div>
      );
    case "boolean":
      return (
        <div className="symbolPropertyItem">
          <input
            type="checkbox"
            onChange={handleCheckboxChange}
            checked={value}
          />
          <span className="checkBoxLabel">{name}</span>
        </div>
      );
    case "Color":
      return (
        <div className="symbolPropertyItem">
          {/* <span className="inputLabel">{name}</span> */}
          <ColorInput
            handleInputChange={handleCustomInputChange}
            value={keys ? getValue(value, keys) : value}
          />
        </div>
      );
    case "Point":
      return (
        <div className="symbolPropertyItem">
          <span className="inputLabel">{name}</span>
          <PointInput
            handleInputChange={handleCustomInputChange}
            value={value}
          />
        </div>
      );
    case "AnchorPoint":
      return (
        <div className="symbolPropertyItem">
          <span className="inputLabel">{name}</span>
          <PointInput
            handleInputChange={handleCustomInputChange}
            value={value}
            unit={"%"}
          />
        </div>
      );
    case "TextSymbol":
      return (
        <div className="symbolPropertyItem">
          {/* <span className="inputLabel">{name}</span> */}
          <TextSymbolInput
            handleInputChange={handleCustomInputChange}
            value={value}
          />
        </div>
      );
    default:
      return (
        <div className="symbolPropertyItem">
          <span className="inputLabel">{name}</span>
        </div>
      );
  }
};

const ColorInput = (props: any) => {
  const [color, setColor] = useState("");
  const debouncedColor = useDebounce(color, 16);

  const value = props.value;
  const handleInputChange = props.handleInputChange;

  useEffect(() => {
    if (debouncedColor) {
      const rgb = hexToRgb(debouncedColor);
      const cimColor = [rgb?.r, rgb?.g, rgb?.b, value[3]];
      handleInputChange(cimColor);
    }
  }, [debouncedColor]);

  const handleCustomInputChange = (event: React.ChangeEvent) => {
    const inputValue = (event.target as HTMLInputElement).value;
    setColor(inputValue);
  };

  const handleOpacityInputChange = (event: React.ChangeEvent) => {
    const inputValue = Number((event.target as HTMLInputElement).value);
    handleInputChange([value[0], value[1], value[2], inputValue]);
  };

  return (
    <div className="colorInputContainer">
      <div>
        <span className="inputLabel">Color</span>
        <input
          type="color"
          value={value ? CIMColorToHex(value) : "#000000"}
          onChange={handleCustomInputChange}
        />
      </div>
      <div>
        <span className="inputLabel">Opcaity</span>
        <input
          type="number"
          value={value ? value[3] : 255}
          onChange={handleOpacityInputChange}
        ></input>
      </div>
    </div>
  );
};

const PointInput = (props: any) => {
  const value = props.value;
  const unit = props.unit;
  const handleInputChange = props.handleInputChange;

  const handleXInputChange = (event: React.ChangeEvent) => {
    let newX = Number((event.target as HTMLInputElement).value);
    if (unit === "%") {
      newX /= 100;
    }
    const newValue = {
      ...value,
      x: newX,
    };
    handleInputChange(newValue);
  };

  const handleYInputChange = (event: React.ChangeEvent) => {
    let newY = Number((event.target as HTMLInputElement).value);
    if (unit === "%") {
      newY /= 100;
    }
    const newValue = {
      ...value,
      y: newY,
    };
    handleInputChange(newValue);
  };

  return (
    <div className="pointInputContainer">
      <div>
        <span>x</span>
        <input
          type="number"
          value={unit === "%" ? value?.x * 100 : value?.x}
          onChange={handleXInputChange}
        ></input>
        <span className="unit">{props.unit}</span>
      </div>
      <div>
        <span>y</span>
        <input
          type="number"
          value={unit === "%" ? value?.y * 100 : value?.y}
          onChange={handleYInputChange}
        ></input>
        <span className="unit">{props.unit}</span>
      </div>
    </div>
  );
};

const TextSymbolInput = (props: any) => {
  const template = TextSymbolTemplate;
  const textSymbolValue = props.value;
  const handleInputChange = props.handleInputChange;

  const handleTextSymbolInputChange = (property: string, newValue: any) => {
    const newSymbol = { ...textSymbolValue };
    newSymbol[property] = newValue;
    handleInputChange(newSymbol);
  };

  return (
    <div className="textInputContainer">
      {template && textSymbolValue
        ? template.map((input: any, index: number) => {
            const value =
              textSymbolValue[input.key] != null
                ? textSymbolValue[input.key]
                : input.keys
                ? textSymbolValue[input.keys[0]]
                : input.default;
            return (
              <SymbolPropertyItem
                key={index}
                value={value}
                propertyName={input.key}
                keys={input.keys}
                inputType={input.type}
                name={input.name}
                unit={input.unit}
                onChange={handleTextSymbolInputChange}
              />
            );
          })
        : null}
    </div>
  );
};
