import React, { useEffect, useRef, RefObject, useContext } from "react";
import { SymbolItemContext } from "../contexts/SymbolItemContext";

import ArcGISMap from "@arcgis/core/Map";
import Basemap from "@arcgis/core/Basemap";
import FeatureLayer from "@arcgis/core/layers/FeatureLayer";
import MapView from "@arcgis/core/views/MapView";
import graphics from "../data/graphics";

export const WebMapView = () => {
  const mapRef = useRef<HTMLDivElement>();
  const { layer, changeLayer } = useContext(SymbolItemContext);

  useEffect(() => {
    const layer = new FeatureLayer({
      source: graphics as any,
      objectIdField: "OBJECTID",
      fields: [
        {
          name: "OBJECTID",
          type: "oid",
        },
      ],
      popupTemplate: {
        content: "ObjectID:{OBJECTID}",
      },
      renderer: {
        type: "simple",
        // symbol: {
        //   type: "simple-marker",
        // },
      } as any,
    });

    const map = new ArcGISMap({
      // basemap: new Basemap({
      //   portalItem: {
      //     id: "75a3ce8990674a5ebd5b9ab66bdab893",
      //   },
      // }),
      layers: [layer],
      basemap: "gray",
    });

    // load the map view at the ref's DOM node
    const view = new MapView({
      container: mapRef.current,
      map: map,
      center: [-118, 34],
      zoom: 6,
    });

    //const configureWidget

    changeLayer(layer);

    return () => {
      if (view) {
        // destroy the map view
        view.container = null as any;
      }
    };
  }, []);

  return <div className="webmap" ref={mapRef as RefObject<HTMLDivElement>} />;
};
